<template>
  <div class="box v-popup" :name="'modal-element-' + product_data.article">
    <div slot="top-right">
      <a title="Закрыть" class="fancybox-item fancybox-close" @click="$emit('close')"> 
      </a>
    </div>
    <div class="box-part" id="bp-left">
      <div class="init-loader"></div>
      <img
        class="v-catalog-item__image"
        :src="require('../../assets/images/gallery/' + product_data.image + '-big.jpg')"
        :alt="product_data.name"
      />
    </div>
    <div class="box-part" id="bp-right">
      <div class="partition" id="partition-card">
        <div class="partition-title v-popup__header">
          {{ product_data.name }}
        </div>
        <div class="partition-info">
          <div class="text-block">
            <!-- <p class="v-catalog-item__name">{{ product_data.name }}</p>  --> 
            {{ product_data.text }} 
          </div>

          <div class="tag-set">
            <span v-for="tag in product_data.category" :key="tag">
              {{ tag }}
            </span>
          </div>
          <div class="row partition-footer">
            <div class="text-left">
              <div class="price bold f26 brown text-left">
                {{ product_data.price | toFix | formattedPrice }}
              </div> 
            </div>
            <div class="text-right">  
              <div class="button-set">
                <button v-if="product_data.reciepe" id="tobuy" @click="productClick">Рецепт</button>
                <a class="button min fancyboxModal" @click="$emit('close'); methodAddToCart(); ">
                  Заказать
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div> 
  </div>
</template>

<script>
import toFix from "../../filters/toFix";
import formattedPrice from "../../filters/price-format";
export default {
  props: {
    product_data: {
      type: Object,
      default: () => {},
    },
    methodAddToCart: { 
      type: Function 
    },
  },  
  filters: {
    toFix,
    formattedPrice,
  },
};
</script>

