<template>
  <div id="app">
    <div class="init-loader"></div> 
    <mobile-menu />
    <v-main-wrapper />
  </div>
</template>

<script>
import mobileMenu from "./components/mobile-menu";
import vMainWrapper from "./components/v-main-wrapper";
import { mapActions, mapGetters } from "vuex";

import $ from "jquery";

export default {
  name: "app",
  components: {
    mobileMenu,
    vMainWrapper,
  },
  computed: {
    ...mapGetters(["IS_MOBILE", "IS_DESKTOP"]),
  },
  methods: {
    ...mapActions(["SET_MOBILE", "SET_DESKTOP"]),
  },
  mounted() {
    // console.log(localStorage);
    // console.log(localStorage.vuex); 
    let vm = this;
    window.addEventListener("resize", function () {
      if (window.innerWidth > 767) {
        vm.SET_DESKTOP();
        console.log("Desktop", vm.IS_DESKTOP);
      } else {
        vm.SET_MOBILE();
        console.log("Mobile", vm.IS_MOBILE);
      }
    });
 

    document.fonts.ready.then(function () {
      $(".init-loader").fadeOut("slow");
      $("#page").addClass("fadeInUp");
    });

    var hPanelHide = 85; // В каком положении полосы прокрутки прятать верхнюю панель
    var top_show = 280; // В каком положении полосы прокрутки начинать показ кнопки "Наверх"
    var speed = 500; // Скорость прокрутки
    var $backButton = $("#up");

    var tempScrollTop,
      currentScrollTop = 0;

    $(window).scroll(function () {
      // При прокрутке попадаем в эту функцию
      /* В зависимости от положения полосы прокрукти и значения top_show, скрываем или открываем кнопку "Наверх" */
      if ($(this).scrollTop() > top_show) {
        $backButton.fadeIn();
      } else {
        $backButton.fadeOut();
      }

      currentScrollTop = $(window).scrollTop();

      if (tempScrollTop < currentScrollTop) {
        if ($(this).scrollTop() > hPanelHide) {
          $(".header-wrapper").addClass("hPanelHide");
        } else {
          $(".header-wrapper").removeClass("hPanelHide");
        }
      } else if (tempScrollTop > currentScrollTop) {
        $(".header-wrapper").removeClass("hPanelHide");
      }
      tempScrollTop = currentScrollTop;
    });

    // scrollto
    window.scrollTo = function (destination, speed) {
      if (typeof speed == "undefined") {
        speed = 800;
      }
      $("html:not(:animated),body:not(:animated)").animate(
        { scrollTop: destination - 60 },
        speed
      );
    };
    $("a.scrollto").click(function () {
      var elementClick = $(this).attr("href");
      var destination = $(elementClick).offset().top;
      scrollTo(destination);
      return false;
    });
    // end scrollTo

    $backButton.click(function () {
      // При клике по кнопке "Наверх" попадаем в эту функцию
      /* Плавная прокрутка наверх */
      scrollTo(0, speed);
    });

    // end

    // tabs

    $("ul.tabs").on("click", "li:not(.current)", function () {
      $(this)
        .addClass("current")
        .siblings()
        .removeClass("current")
        .closest("div.section")
        .closestChild("div.box")
        .removeClass("visible")
        .eq($(this).index())
        .addClass("visible");
    });

    $("ul.tabs.mobile li").click(function () {
      $(this)
        .parent()
        .hide()
        .siblings(".mobile-tab-header")
        .html($(this).html());
      $(".mobile-tab-header").removeClass("active");
    });

    $(".mobile-tab-header").click(function (e) {
      $(this).toggleClass("active");
      $(this).siblings(".tabs.mobile").toggle();
      e.stopPropagation();
    });

    $("body").click(function () {
      if ($(".mobile-tab-header").is(":visible")) {
        $(".tabs.mobile").hide();
        $(".mobile-tab-header").removeClass("active");
      }
    });

    // end tabs

    // Mobile menu

    $(".mob-menu-btn").click(function () {
      $(".mobile-menu").addClass("open");
    });

    $(".mobile-menu-close").click(function () {
      $(".mobile-menu").removeClass("open");
    });

    $(".mobile-menu ul a").click(function () {
      $(".mobile-menu").removeClass("open");
    });

    $(".mobile-menu, .mob-menu-btn").click(function (e) {
      e.stopPropagation();
    });
    $("body").click(function () {
      $(".mobile-menu").removeClass("open");
    });

    // End mobile menu
  },
};
</script>

