<template>
  <div class='v-header'>
    <div class="header-wrapper">
      <div class="container">
        <div :class="['header', {'no-basket': !basketOn}]">

          <div class="logo">
            <router-link :to="{name: 'mainPage'}">
              Wally Dessert
            </router-link>
          </div>
          
          <!-- * * * * * * Top menu * * * * * * --> 
          <div class="top-menu hidden-sm hidden-xs">
            <main-menu /> 
          </div>
          <!-- * * * * * * Top menu: end* * * * * * -->

          <div v-if="basketOn" class="basket" id="click-on-it">
            <router-link :to="{name: 'cart', params: {cart_data: CART} }">
              <img src="../../assets/images/Icon-basket.svg" />
              <b v-if="CART.length !== 0">{{CART.length}}</b>
            </router-link>
          </div> 
          
          <div class="mob-menu-btn hidden-md hidden-lg right">
            <span></span>
            <span></span>
            <span></span>
          </div>

        </div>
      </div>
    </div>
    <div class="header-push"></div>
    <!-- <v-notification
        :messages="messages"
    /> -->
  </div>
</template>

<script> 
import { mapGetters } from 'vuex'
import mainMenu from './main-menu.vue'

export default {
  components: { mainMenu },
  name: "v-header", 
  data() {
    return {
      basketOn: false
    }
  },
  computed: {
    ...mapGetters([
      'CART', 
    ]),  
    // cartCount(CART) {
    //   return CART.length;
    // }
  },  
  mounted() {
    this.basketOn = this.CART.length > 0; 
  },
  watch: {
    CART() {
      this.basketOn = this.CART.length > 0;
    }
  }
}
</script>

<style lang="scss">
.v-header-test {
  background: $green-bg;
}
</style>
