<template>
  <div>
    <a href="/#order" @click.prevent="show" class="button big">Заказать</a>
    <modal
      name="modal-order"
      @opened="opened"
      :adaptive="true"
      :height="440" :width="400"
      id="order">
      <div class="form-application">
        <div class="title-h2 black">Оформление заявки</div>
        <div class="push10"></div>
        <div class="rf">
          <form
            name="form-application"
            action="https://formspree.io/f/mbjbaopo"
            method="POST"
          > 
            <div slot="top-right">
              <a
                title="Закрыть"
                class="fancybox-item fancybox-close"
                @click="hide"
              >
              </a>
            </div>
            <div class="form-group">
              <input
                type="hidden"
                name="order_list"
                id="themeInput"
                class="form-control"
                readonly="readonly"
                :value="concatOrderList"
              />
            </div>

            <div class="form-group">
              <input
                type="text"
                name="fio"
                class="form-control"
                placeholder="Ваше имя"
                ref="fio"
                maxlength="50"
              />
            </div>

            <div class="form-group">
              <masked-input
                mask="\+\7 (111) 111-11-11"
                name="tel"
                class="form-control required tel"
                type="text"
                placeholder="Телефон*"
              />
            </div>
            <p>
              <span class="red">*</span>
              <span class="f12">- поля, обязательные для заполнения</span>
            </p>
            <div>
              <input type="submit" class="button btn feedback" value="Отправить" />
            </div>
            <input type="hidden" name="_total" id="order-total" :value="cartTotalCost">
            <input type="hidden" name="_subject" id="email-subject" value="Заказ">
          </form>
        </div>
      </div>
      
    </modal>
  </div>
</template>

<script>
import MaskedInput from "vue-masked-input";
export default {
  components: {
    MaskedInput,
  },
  props: {
    order_list: {
      type: Array,
      default() {
        return [];
      },
    },
    cartTotalCost: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      concatOrderList: "",
    };
  },
  methods: {
    concatOrder() {
      if (this.order_list.length > 1) {
        for (const iterator of this.order_list) {
          this.concatOrderList += iterator.name + ' ('+ iterator.quantity +'),';
        }
      } else { 
        this.concatOrderList = this.order_list[0].name + ' ('+ this.order_list[0].quantity +')';
      }
    },
    show() {
      this.$modal.show("modal-order");
      this.concatOrder();
    },
    showLogin() {
      this.$modal.show("modal-login");
      this.$modal.hide("modal-order");
    },
    opened() {
      this.$refs.fio.focus();
    },
    hide() {
      this.$modal.hide("modal-order");
    },
  },
};
</script>
<style scoped>
.form-application {
    padding: 55px 40px 40px!important;
}
</style>
