<template>
  <div class="mobile-menu" id="mobmenu">
    <span class="mobile-menu-close close"></span>
    <main-menu @elClick="closeMobileMenu" />
  </div>
</template>

<script>
import mainMenu from "./layouts/main-menu.vue";

export default {
  components: { mainMenu },
  name: "mobile-menu",
  props: {},
  data() {
    return {};
  },
  computed: {},
  methods: {
    closeMobileMenu() {
      document.querySelector(".mobile-menu-close.close").click();
    },
  },
};
</script>

<style scoped>
.close {
  cursor: pointer;
  position: absolute;
  right: 0px;
  top: 0px;
  width: 70px;
  height: 70px;
  z-index: 30;
}

.close::before,
.close::after {
  position: absolute;
  left: 0;
  top: 16px;
  content: "";
  height: 35px;
  width: 2px;
  background-color: white;
  transition: all 150ms ease-in-out;
  right: 0;
  bottom: 0;
  margin: 0 auto;
}
.close::after {
  transform: rotate(-45deg);
}

.close::before {
  transform: rotate(45deg);
}
</style>
