<template>
  <div class="v-cart container relative">
    <h1 v-if="CART.length">Корзина</h1>
    <div class="v-cart__fullwindow-section">
      <div v-if="!CART.length">
        <div class="not-found-text" style="text-align: center;"> 
          <h1> Заказ пустой... </h1> 
          <br>
          <router-link :to="{name: 'mainPage'}">← Перейти на главную</router-link> 
      </div> 
      </div>
      <v-cart-item
        v-for="(item, index) in CART"
        :key="item.article"
        :cart_item_data="item"
        @deleteFromCart="deleteFromCart(index)"
        @increment="increment(index)"
        @decrement="decrement(index)"
      />
      <div v-if="CART.length" class="v-cart__total">
        <div class="total__group">
          <p class="total__name">Сумма:</p>
          <p>{{ cartTotalCost | toFix | formattedPrice }}</p>
        </div>
        <!-- TODO: повесить закрытие модалки и очистку корзины -->
        <modal-order :order_list="CART" :cartTotalCost="cartTotalCost"> Заказ </modal-order>  
      </div>
    </div>
  </div>
</template>

<script>
import vCartItem from "./v-cart-item";
import toFix from "../../filters/toFix";
import formattedPrice from "../../filters/price-format";
import ModalOrder from '../popup/ModalOrder.vue';

import { mapActions, mapGetters } from "vuex";

export default {
  name: "v-cart",
  components: {
    vCartItem,
    ModalOrder,
  },
  props: {
    cart_data: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  data() {
    return {};
  },
  filters: {
    formattedPrice,
    toFix,
  },
  computed: {
    ...mapGetters([
      'CART', 
    ]),  
    cartTotalCost() {
      let result = [];

      if (this.CART.length) {
        for (let item of this.CART) {
          result.push(item.price * item.quantity);
        }

        result = result.reduce(function (sum, el) {
          return sum + el;
        });
        return result;
      } else {
        return 0;
      }
    },
  },
  methods: {
    ...mapActions([
      "DELETE_FROM_CART",
      "INCREMENT_CART_ITEM",
      "DECREMENT_CART_ITEM",
    ]),
    increment(index) {
      this.INCREMENT_CART_ITEM(index);
    },
    decrement(index) {
      this.DECREMENT_CART_ITEM(index);
    },
    deleteFromCart(index) {
      this.DELETE_FROM_CART(index);
    },
  },
  watch: {
    CART() {
      if (this.CART.length === 0) {
        this.$router.push({ name: "mainPage", hash: "#applicationsection" });
      }
    },
  }, 
};
</script>

<style lang="scss">
.v-cart {
  display: flex;
  flex-direction: column;
  margin-top: 80px;
  margin-bottom: 80px;
  min-height: 50vh;
  &__fullwindow-section {
    flex: 1 1 100%;
  }
  &__total {
    padding: $padding * 2 $padding * 3;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: url(../../assets/images/bg2.png);
    color: #4b3939;
    font-size: 20px;
  }

  .total__group {
    display: flex;
    justify-content: center;
  }
  .total__name {
    margin-right: $margin * 2;
  }
}
</style>
