<template>
    <section class="contacts-section relative" id="contacts">
        <div class="push60"></div>
        <div class="container relative">
            <div class="title text-center f48 brown nautilus">
                <img :src="require(`@/assets/images/brown-cake-icon.png`)" alt="brown-cake-icon" /><br />
            </div>
            <div class="push30"></div>
            <div class="row">
                <div class="col-sm-6">
                    <div class="myimage-block">
                    </div>
                </div>
                <div class="col-sm-6">
                    <div class="contacts-block">
                        <div class="title text-left f48 brown nautilus">
                            <span>Привет!</span>
                        </div>
                        <div class="push10"></div>
                        <div class="f16">
                            Меня зовут Валентина Климова.
                            Я делаю отличные <a class="scrollto" href="#applicationsection">кондитерские изделия</a> — cпециализируюсь на тортах, капкейках, безе.
                            Вы можете написать мне по дополнительным вопросам и сотрудничеству, воспользовавшись этими контактами:
                        </div>
                        <div class="push15"></div>
                        <!-- <div class="element"><i class="fa fa-phone" aria-hidden="true"></i> +7 (966) 757-51-07</div> -->
                        <div class="element"><i class="fa fa-envelope" aria-hidden="true"></i> <a
                                href="mailto:wallydessert@gmail.com">wallydessert@gmail.com</a></div>
                        <div class="element"><i class="fa fa-clock-o" aria-hidden="true"></i>
                            Пн-Пт: с 9.00 до 18.00
                        </div>
                        <div class="element"><i class="fa fa-telegram" aria-hidden="true"></i> 
                            <a href="https://t.me/Valentina_Klimova" target="_blank" rel="noopener noreferrer">@Valentina_Klimova</a>
                        </div>
                    </div>
                </div>
            </div>
            <div class="push60"></div>
            <div class="row">
                <div class="col-sm-12">
                    <div class="feedback-block">
                        <div class="f22 neucha upper brown">Связаться со мной</div>
                        <div class="push17"></div>
                        <div class="rf">
                            <form name="form-feedback">
                                <p v-if="errorFields.size > 0">
                                    <b>Ошибки заполнения:</b>
                                    <ul>
                                        <li v-for="error in errorFields" :key="error">{{ error }}</li>
                                    </ul>
                                </p>
                                <div class="row min">
                                    <div class="col-sm-6">
                                        <div class="form-group">
                                            <input type="text" name="fio" required class="form-control required"
                                                placeholder="Имя*" maxlength="50" />
                                        </div>
                                    </div>
                                    <div class="col-sm-6">
                                        <div class="form-group">
                                            <input type="email" @blur="validateEmail" v-model="formFields.email" name="email" required class="form-control required"
                                                placeholder="E-mail*" />
                                                {{ formFields.email }}
                                        </div>
                                    </div>
                                </div>
                                <div class="form-group">
                                    <textarea name="message" required class="form-control required"
                                        placeholder="Сообщение*"></textarea>
                                </div>
                                <div class="push10"></div>
                                <div class="button__wrap">
