    </section>
</template>

<script> 
export default {
  name: "section-contacts",
  components: { 
  },
  props: {},

  data() {
    return {
        formFields: {
            name: '',
            email: '',
            message: ''
        },
        errorFields: new Map(), 
    };
  },
  computed: {},
  methods: {
    validateEmail() {
        if (/^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(this.formFields.email)) {
            this.errorFields.delete('email');
        } else {
            this.errorFields.set('email', 'Please enter a valid email address'); 
        }
        console.log("blur");
        console.log(this.errorFields.size);
    }

    // $('.super-offer-section-bottom').parallax("50%", 0.4);
  },
};
</script>

<style scoped>
.feedback-block {
    display: none;
}
#contacts {
    position: relative;
    z-index: 15;
}
</style>
