<template>
  <fragment>
    <div
      :class="[
        'col-sm-6 col-md-4 col-lg-3 product-list-item mix all',
        product_data.category,
        { 'show-info': isShowElInfo },
      ]"
    >
      <div class="element">
        <div
          class="element-inner"
          @mouseover="isShowElInfo = true"
          @mouseleave="isShowElInfo = false"
        >
          <!-- <div class="element-inner"> -->
          <div
            class="img-wrapper text-center relative"
            @click="showModalEl"
            @addToCart="addToCart"
          >
            <a class="element___link-modal"> </a>
            <img
              :src="require('../../assets/images/gallery/' + product_data.image + '.jpg')"
              :alt="product_data.name"
            />
          </div>
          <div class="element-content" @click="showModalEl">
            <div class="title">{{ product_data.name }}</div>
            <div class="push5"></div>
            <div class="text">
              {{ product_data.text }}
            </div>
            <div class="push15"></div>
            <div class="row">
              <div class="col-xs-6">
                <div class="price bold f26 brown text-left">
                  {{ product_data.price | toFix | formattedPrice }}
                </div>
              </div>
              <div class="col-xs-6 text-right">
                <a
                  href="#application"
                  class="button min fancyboxModal"
                  @click="addToCart"
                  >Заказать</a
                >
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <hr class="clear-hr" />
  </fragment>
</template>

<script>
import toFix from "../../filters/toFix";
import formattedPrice from "../../filters/price-format";
import { Fragment } from "vue-fragment";

import ModalElement from "@/components/popup/ModalElement.vue";

export default {
  name: "v-catalog-item",
  components: {
    Fragment,
  },
  props: {
    product_data: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  data() {
    return {
      isShowElInfo: false,
    };
  },
  filters: {
    toFix,
    formattedPrice,
  },
  computed: {},
  methods: {
    productClick() {
      this.$emit("productClick", this.product_data.article);
    },
    showModalEl() {
      this.$modal.show(
        ModalElement,
        {
          product_data: this.product_data,
          methodAddToCart: this.addToCart,
        },
        {
          height: "auto",
          width: "900",
          adaptive: true,
        }
      );
    },
    addToCart() {
      this.$emit("addToCart", this.product_data);
    },
  },
  mounted() {
    this.$set(this.product_data, "quantity", 1);

    // document.addEventListener("DOMContentLoaded", function () {

    //   // lightgallery begin
    //   console.log(this);

    //   window.lightGallery(document.getElementById('lightgallery'), {
    //     selector: '.absolute',
    //     thumbnail: false
    //   });
    //   // light gallery end

    //   document.querySelector('.absolute').addEventListener("click", myFunction);

    //   function myFunction(e) {
    //     e.preventDefault();
    //     e.stopPropagation();
    //     console.log("Hello World");
    //   }
    // })
  },
};
</script>

<style lang="scss">
#catalog {
  .element-content { 
    position: absolute;
    width: 100%;
    transition: 200ms all;
    bottom: -175px;
    color: white;
    z-index: 90;
    cursor: pointer;
    
    .title {
      color: white;
    }
  }
  .show-info .element-content {
    bottom: -65px;
  }
}
</style>
 