<template>
  <div class="v-cart-item">
    <div class="v-cart-item__image">
      <img
        :src="require('../../assets/images/gallery/' + cart_item_data.image + '.jpg')"
        :alt="cart_item_data.name"
      />
    </div>
    <div class="v-cart-item__info">
      <b class="v-cart-item__name">{{ cart_item_data.name }}</b>
      <p class="v-cart-item__price">
        {{ cart_item_data.price | toFix | formattedPrice }}
      </p>
      <!-- <p>{{cart_item_data.article}}</p> -->
    </div>
    <div class="v-cart-item__quantity">
      <span class="quantity__tools">
        <span class="quantity__btn" @click="decrementItem">-</span>
        {{ cart_item_data.quantity }}
        <span class="quantity__btn" @click="incrementItem">+</span>
      </span>
    </div>
    <button class="btn-delete">
      <i class="close" @click="deleteFromCart"></i>
    </button>
  </div>
</template>

<script>
import toFix from "../../filters/toFix";
import formattedPrice from "../../filters/price-format";

export default {
  name: "v-cart-item",
  props: {
    cart_item_data: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  data() {
    return {};
  },
  filters: {
    toFix,
    formattedPrice,
  },
  computed: {},
  methods: {
    decrementItem() {
      this.$emit("decrement");
    },
    incrementItem() {
      this.$emit("increment");
    },
    deleteFromCart() {
      this.$emit("deleteFromCart");
    },
  },
};
</script>

<style lang="scss">
.v-cart-item {
  position: relative;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 0 8px 0 #e0e0e0;
  padding: $padding * 2;
  margin-bottom: $margin * 2;
  &__name {
    font-size: 30px;
  }
  &__image {
    max-width: 17%;
  }
  &__info {  
    flex: .3 1 55%;
    padding: 0 25px;
  }
  &__price {
    font-size: 25px;
    font-weight: 400;
  }
  &__quantity {
    display: flex;
    width: 10%;
    flex-basis: 10%;
    flex-wrap: nowrap;
    justify-content: center;
    font-size: 1.1em;

    .quantity__btn {
      cursor: pointer;
      display: flex;
      flex: 1 1 35px;
      text-align: center;
      justify-content: center;
    }

    .quantity__tools {
      display: flex;
      width: 100%;
      flex-wrap: nowrap;
      justify-content: space-around;
      user-select: none;
    }
  }
  .btn-delete {
    border: none;
    background: none;
    cursor: pointer;
    outline: none;
    position: relative;
    top: 0;
    right: 0;
    width: 25px;
    height: 25px;
    padding: 2px;
    .close {
      cursor: pointer;
      position: absolute;
      right: 0px;
      top: 0px;
      width: 30px;
      height: 30px;
      z-index: 30;
    }

    .close::before,
    .close::after {
      position: absolute;
      left: 15px;
      top: 10px;
      content: "";
      height: 11px;
      width: 1px;
      background-color: #e36060;
      -webkit-transition: all 150ms ease-in-out;
      -o-transition: all 150ms ease-in-out;
      transition: all 150ms ease-in-out;
    }
    .close::after {
      transform: rotate(-45deg);
    }

    .close::before {
      transform: rotate(45deg);
    }
  }
}

@media only screen and (max-width: 767px) {
  .v-cart-item {
    flex-wrap: wrap;
    justify-content: space-around;
    &__image {
      max-width: 45%;
      margin-bottom: 21px;
    }
    &__quantity {
      width: 23%;
      flex-basis: 23%;
    }
    .btn-delete {
      position: absolute;
    }
  }
}
</style>
