<template>
  <ul v-if="menuitems">
    <li
      v-for="menu in menuitems"
      :key="menu.index"
      :class="{ active: currSection === menu.href }"
    >
      <router-link
        :to="{ name: 'mainPage', hash: '#' + menu.href }"
        @click="$emit('elClick')"
      >
        {{ menu.name }}
      </router-link>
    </li>

    <!-- <li v-for="menu in menuitems" :key="menu.id"> 
      <a :href="'#'+menu.href">{{menu.name}}</a> 
    </li> -->
  </ul>
</template>

<script> 
import $ from "jquery";

export default {
  name: "main-menu",
  data() {
    return {
      menuitems: [
        {
          id: 101,
          name: "Главная",
          href: "home",
        },
        {
          id: 102,
          name: "Каталог изделий",
          href: "applicationsection",
        },
        {
          id: 103,
          name: "Подбор",
          href: "superoffer",
        },
        {
          id: 104,
          name: "Контакты",
          href: "contacts",
        },
      ],
      currSection: "home",
    };
  },
  methods: { 
    handleScroll() {
      let vm = this;
      $("section").each(function () {
        let currPosition = $(document).scrollTop() + 150;
        if (
          $(this).position().top <= currPosition &&
          $(this).position().top + $(this).outerHeight() > currPosition
        ) {
          vm.currSection = $(this).attr("id") === undefined ? "home" : $(this).attr("id");
        }
      });
    },
  }, 
  mounted() { 
    window.addEventListener("scroll", this.handleScroll);
  },
  updated: () => {
    // $(document).scroll(function () {
    // });
  },
  created() {},
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },
};
</script>

