<template>
  <div>
    <ul class="simplefilter controls text-center-md">
      <li v-for="filteroption in categories" :key="filteroption.value" :class=" ['filter']"
        :data-filter="'.'+filteroption.value">
        <img :src="require(`@/assets/images/filter-icon-${filteroption.value}.png`)"
          :alt="'иконка ' + filteroption.name">
        {{ filteroption.name }}
      </li>
    </ul>
    <div class="push30"></div>
    <div class="application-inner">
      <div class="row">
        <div class="mix-container">
          <v-catalog-item v-for="(product, index) in sortedProducts" :key="product.article" :data-index="index"
            :product_data="product" @addToCart="addToCart" @productClick="productClick" />
        </div>
      </div>
    </div>
    <VNotification :messages="messages" />
  </div>
</template>

<script>
import mixitup from 'mixitup';
// import gsap from 'gsap';
// import '../../../../node_modules/lghtglr/lightgallery.js'
// import '../../../../node_modules/lghtglr/dist/css/lightgallery.css'

import VNotification from '@/components/notifications/v-notification.vue';
import VCatalogItem from '@/components/catalog/v-catalog-item';

import { mapActions, mapGetters } from 'vuex'
import axios from "axios"; 
export default {
  name: "simple-filter-btns",
  components: {
    VNotification,
    VCatalogItem 
  },
  data() {
    return {
      categories: [
        { id: 0, name: 'Все', value: 'all' },
        { id: 1, name: 'детские', value: 'kids' },
        { id: 2, name: 'свадебные', value: 'wedding' },
        { id: 3, name: 'новогодние', value: 'christmas' },
        { id: 4, name: 'праздничные', value: 'festive' },
        { id: 5, name: 'другие', value: 'other' },
      ],
      selected: 'Все',
      selectedSort: '',
      sortedProducts: [
      ],
      minPrice: 0,
      maxPrice: 10000,
      messages: [],
    }
  },
  props: {
    productCards: {
      type: Array,
      default: () => []
    },
  },
  methods: {
    ...mapActions([
      'GET_PRODUCTS_FROM_API',
      'ADD_TO_CART'
    ]),
    productClick(article) {
      this.$router.push({ name: 'product', query: { 'product': article } })
    },
    addToCart(data) {
      this.ADD_TO_CART(data)
        .then(() => {
          let timeStamp = Date.now().toLocaleString();
          this.messages.unshift(
            { name: 'Товар добавлен в корзину', icon: 'check_circle', id: timeStamp }
          );
          this.$router.push({ name: 'cart', params: {cart_data: this.CART}  })
        })
    },
  },
  computed: {
    ...mapGetters([
      'PRODUCTS',
      'CART',
      'IS_MOBILE',
      'IS_DESKTOP',
      'SEARCH_VALUE'
    ]),
  },
  mounted() {
    this.GET_PRODUCTS_FROM_API()
      .then((response) => {
        if (response.data) {
          this.sortedProducts = [...this.PRODUCTS];

          // TODO - избавиться от костыля: повторный запрос что бы дождаться отрисовки пунктов меню
          axios.get("https://wallydessert.ru/db.json").then(() => {
            var containerMix = document.querySelector('.mix-container');
            mixitup(containerMix, {
              load: {
                filter: '.all',
              },
              // animation: {
              //   effects: 'fade rotateZ(-180deg)', /* fade scale */
              //   duration: 700 /* 600 */
              // }, 
            });
          });
        }
      });
  }

}
</script>

<style lang="scss">
.mix-container {
  position: relative;
  overflow: hidden;
}

.simplefilter {
  position: relative;
  z-index: 0;
}

.simplefilter .mixitup-control-active {
  background: #4B3939;
  border-color: #4B3939;
  color: #fff;
}
</style>
