<template>
  <section id="home"> 
    <div class="top-slider white relative">
      <VueSlickCarousel v-bind="settings">
        <v-slider-item
          v-for="item in addSrcToStyle"
          :key="item.id"
          :item_data="item"
        />
      </VueSlickCarousel>
    </div>
  </section>
</template>

<script>
import vSliderItem from "@/components/layouts/v-slider-item.vue";
import VueSlickCarousel from "vue-slick-carousel";
import "vue-slick-carousel/dist/vue-slick-carousel-theme.css";

export default {
  name: "section-top-slider",
  components: {
    vSliderItem,
    VueSlickCarousel,
  },
  props: {
    carousel_data: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      settings: {
        centerMode: true,
        centerPadding: "0px",
        dots: true,
        autoplay: true, 
        autoplaySpeed: 5000,
        speed: 1500,
        infinite: true,
        slidesToShow: 1,
        slidesToScroll: 1,
        swipeToSlide: true,
      },
      sliderItems: [
        {
          id: 1,
          name: "img1",
          text_title:
            'Торты на день рождения <br class="visible-lg">или юбилей',
          text_text: "от минималистичных до пышных",
          btn: "Подобрать торт",
          btn_link: "#superofferbottom",
          img: "slide1.jpg",
        },
        // {
        //   id: 2,
        //   name: "img2",
        //   text_title: "Румяные и вкусные “Краффины”",
        //   text_text: "Ваши гости останутся довольны",
        //   btn: "Подобрать выпечку",
        //   btn_link: "#superofferbottom",
        //   img: "slide2.jpg",
        // },
        {
          id: 3,
          name: "img3",
          text_title: "Яркие снаружи, мягкие внутри",
          text_text: "Разнообразные капкейки, маффины",
          btn: "Подобрать",
          btn_link: "#superofferbottom",
          img: "slide3.jpg",
        },
      ],
    };
  },
  computed: {
    addSrcToStyle() {
      this.sliderItems.forEach((item, index, array) => {
        array[index]["item_style"] =
          "url(" + require(`@/assets/images/${item.img}`) + ") 50% 50% no-repeat";
      });
      return this.sliderItems;
    },
  },
};
</script>

