import Vue from 'vue'
import Router from 'vue-router'
import $ from "jquery";   

import vCatalog from '../components/catalog/v-catalog'
import vCart from '../components/cart/v-cart'
import vMainPage from '../components/main-page/v-main-page'
import vProductPage from '../components/catalog/v-product-page'
import vNotFound from '../components/v-404'

Vue.use(Router);

let router = new Router({
  routes: [
    {
      path: '/',
      name: 'mainPage',
      component: vMainPage
    }, 
    {
      path: '/catalog',
      name: 'catalog',
      component: vCatalog
    },
    {
      path: '/product',
      name: 'product',
      component: vProductPage
    },
    {
      path: '/cart',
      name: 'cart',
      component: vCart,
      props: true
    },
    {
      path: '/:pathMatch(.*)*',
      name: 'notFound',
      component: vNotFound
    }
  ], 
  mode: 'history',
  scrollBehavior (to) { 
    if (to.hash) {
        const offsetTop =  $(to.hash).offset().top - 61;
        $('html, body').stop().animate({
            scrollTop: offsetTop
        }, 500); 
        return { selector: to.hash } 
    } 
  }
})

router.beforeEach((to, from, next)=>{ 
  const redirectFrom = localStorage.getItem('path');
  if (redirectFrom && router.options.routes.some((routeItem) => routeItem.path.slice(1) === redirectFrom) &&
  // ❗️ Avoid an infinite redirect
  to.path.slice(1) !== redirectFrom) {
    localStorage.removeItem('path');
    next({ path: '/' + redirectFrom, replace: true })
  } else {
    next();
  } 
})

export default router;
